*,
*::before,
*::after {
	box-sizing: border-box;
}

html {
	font-size: 16px;
	width: 100vw;
}

html,
body {
	min-height: 100vh;
	position: relative;
}

body {
	font-size: 1rem;
	font-family: 'Montserrat', sans-serif;
	font-weight: 400;
	background: #F8F9FA;
	display: flex;
	flex-direction: column;
	overflow-x: hidden;

	@include media-breakpoint-down(md) {
		padding-bottom: 52px;
	}

	@include media-breakpoint-down(sm) {
		padding-bottom: 50px;
	}

	@media (max-width: 359.98px) {
		padding-bottom: 32px;
	}

    &.no-scroll {
        height: 100%;
    }

	&.fancybox-active,
	&.no-scroll {
		overflow: hidden;
	}

	// Theme colors CSS here

	// &[data-theme="default"] {
	// 	.primary-backgroud-color {
	// 		background: $primaryColor;
	// 	}
	// 	// .secondary-backgroud-color {
	// 	// 	background: $secondaryColor;
	// 	// }
	// }

	// &[data-theme="blue"] {
	// 	.primary-backgroud-color {
	// 		background: blue;
	// 	}
	// 	.secondary-backgroud-color {
	// 		background: darkblue;
	// 	}
	// }
}

.content-block {
	@include content-block;

	h3,
	h4,
	h5,
	h6 {
		font-weight: 500;
		margin-bottom: 1rem;
		margin-top: 1rem;
	}

	p,
	ul,
	ol {
		color: $darkTextColor;
		font-size: 14px;

		& + p,
		& + ul {
			margin-top: 1rem;
		}
	}

	ul,
	ol {
		padding-left: 1rem;
	}

	li::marker {
		font-size: 0.8rem;
	}

	hr {
		background-color: $primaryLightColor;
		opacity: 1;
	}

	&-text {
		a {
			color: $linksColor;
			text-decoration: underline;

			&:hover {
				text-decoration: none;
			}
		}
	}

	&.no-hover-shadow {
		&:hover {
			box-shadow: none;
		}
	}
}

.content-block-p24 {
	@include content-block(1.5rem);
}

.mobile-no-gutters {

	// Add to .container to apply class
	@include media-breakpoint-down(md) {
		padding-left: 0;
		padding-right: 0;
		overflow: hidden;

		main > .row {
			--bs-gutter-x: 0;
		}

		.sc-page-title,
		nav[aria-label="breadcrumb"] {
			padding-left: 1rem;
			padding-right: 1rem;
		}

		.mobile-no-radius {
			border-radius: 0;
		}
	}
}

@include media-breakpoint-down(md) {
	.sc-page-title {
		margin-bottom: 0;

		h1 {
			font-size: 1.25rem;
		}
	}
}

.red,
.required {
    color: $redColor !important;
}

.blue-link {
    color: $linksColor;
	text-decoration: underline;
	cursor: pointer;

	&:hover {
		text-decoration: none;
		color: $linksColor;
	}
}

.light-text {
	color: $lightTextColor;
}

.dark-text {
	color: $darkTextColor;
}

.br-2 {
	border-radius: 0.125rem !important;
}

.br-4 {
	border-radius: 0.25rem !important;
}

.br-6 {
	border-radius: 0.375rem !important;
}

.br-8 {
	border-radius: 0.5rem !important;
}

.br-12 {
	border-radius: 0.75rem !important;
}

.error_style {
    background-color: #FFEEF5 !important;
    border: 1px solid #FE4D97 !important;
    transition: background-color $mainTransition, border $mainTransition;

	& + label {
	    color: $redColor !important;
	}
}

.sc-no-stock {
	filter: grayscale(100%);
}

@media (min-width: $break-xxl) {
	.container,
	.container-fluid,
	.container-sm,
	.container-md,
	.container-lg,
	.container-xl,
	.modal-dialog-1110 {
		max-width: 1600px !important;
	}
}

// Dropdown
.sc-dropdown-box {
	display: inline-block;
	position: relative;

	&.active {
		cursor: pointer;

		.sc-dropdown {
			opacity: 1;
			margin-top: 0;
			z-index: 10000;
			visibility: visible;

			&-toggle {
				color: $linksColor;
				text-decoration: underline;
				cursor: pointer;
				z-index: 2;

				&::after {
					transform: rotate(180deg);
					background: $linksColor;
				}
			}
		}
	}
}

.sc-dropdown {
	position: absolute;
	top: calc(100% - 0.5rem);
	left: 50%;
	right: auto;
	transform: translate(-50%, 0);
	min-width: 200px;
	opacity: 0;
	z-index: -1;
	margin-top: 20px;
	padding-top: 20px;
	transition: opacity $mainTransition 0.1s, margin-top $mainTransition 0.1s;
	visibility: hidden;
	cursor: default;

	&-inner {
		background: #FFFFFF;
		box-shadow: 0px 2px 8px rgba(40, 41, 61, 0.04), 0px 16px 24px rgba(96, 97, 112, 0.16);
	}

	&-toggle {
		cursor: pointer;

		&::after {
			content: '';
			display: inline-block;
			mask: url("../img/sprite.svg#include--top-dropdown-toggle");
			mask-size: cover;
			width: 12px;
			height: 12px;
			margin-left: 0.25rem;
			background: inherit;
			transition: transform $mainTransition;
		}
	}

	&::after {
		content: '';
		position: absolute;
		left: 50%;
		transform: translateX(-50%);
		top: 0;
		border: 10px solid transparent;
		border-top: 10px solid #FFFFFF;
		transform: rotate(180deg);
	}
}

